<template>
  <div ref="dom" class="charts chart-bar" />
</template>

<script>
import echarts from 'echarts'
import tdTheme from './theme.json'
import { on, off } from './tools'

echarts.registerTheme('tdTheme', tdTheme)
export default {
    name: 'ChartBar',
    props: {
        value: Object,
        text: String,
        subtext: String,
    },
    data() {
        return {
            dom: null,
        }
    },
    mounted() {
        this.$nextTick(() => {
            const xAxisData = Object.keys(this.value)
            const seriesData = Object.values(this.value)
            const option = {
                title: {
                    text: this.text,
                    subtext: this.subtext,
                    x: 'center',
                },
                xAxis: {
                    type: 'category',
                    data: xAxisData,
                },
                yAxis: {
                    type: 'value',
                },
                series: [{
                    data: seriesData,
                    type: 'bar',
                }],
            }
            this.dom = echarts.init(this.$refs.dom, 'tdTheme')
            this.dom.setOption(option)
            on(window, 'resize', this.resize)
        })
    },
    beforeDestroy() {
        off(window, 'resize', this.resize)
    },
    methods: {
        resize() {
            this.dom.resize()
        },
    },
}
</script>
